<template lang="pug">

    v-card 
        v-card-title 
            h2 筛选上传数据库

        v-card-text(class="pa-5")
            v-data-table(
                :headers="filter_header"
                :items="filter_data"
                hide-default-footer
            )
                //- 事件
                template(v-slot:item.has="{item}")
                    v-chip(color ="success" v-if="item.has") 
                    v-chip(color ="error" v-else) {{item.has}}

                template(v-slot:top)
                    v-toolbar(flat class="mb-8")
                        v-row
                            v-col(cols="12" lg="6")
                                v-combobox(
                                    v-model="collection"
                                    :items="collections"
                                    label="选择数据库"
                                )
                            v-col(cols="12" lg="3")
                                v-chip(pill color="success")
                                    v-avatar(left)
                                        v-btn(color="blue lighten-1" class="white--text")
                                            v-icon(small) fas fa-check
                                    v-avatar {{must_num-lack_num}}

                            v-col(cols="12" lg="3")

                                v-chip(pill color="error")
                                    v-avatar(left)
                                        v-btn(color="error lighten-1" class="white--text")
                                            v-icon(small) mdi-bug
                                    v-avatar {{lack_num}}
  
  
                        
                        
                                    

</template>

<script>
import { sync,get } from 'vuex-pathify'
export default {
    name: "UploadFilterView",
    data:() => ({
        collection:"",
        must_num:10,
        lack_num:4,
        // filter_data:[
        //     {dbfield:"wellname",showfield:"井号",ismust:true,has:true},
        //     {dbfield:"blockname",showfield:"区块",ismust:true,has:false},
        //     ],
        filter_header:[{text:"数据库字段",value:"dbfield"},{text:"显示字段",value:"showfield"},{text:"必须字段",value:"ismust"},{ text: "状态", value: "has" }]
    }),

    methods:{
        
        
    },
    computed:{
        // 获取用户有权限的数据库
        collections:get('user/collections'),
        headers:get('db/headers'),

        filter_data(){


            let ismust={};
            for (var i in this.collection_config.mustfields){
                ismust[this.collection_config.mustfields[i]]="是";
            }
 
            let filterdata=this.headers.map(item=>({
                    dbfield:item.value,showfield:item.text,ismust:ismust[item.value],has:this.csv_header_map[item.value],
                }))
            return filterdata
        },

        collection_config:sync('db/collection_config'),
        all_filter_values:sync('db/all_filter_values'),
        csv_header_map:get('db/csv_header_map'),
    },
    watch:{
        // 更新数据库
        collection(){
            this.$store.dispatch("db/update_collection",this.collection.value)
        },
    }

}
</script>